import React, { PropTypes } from 'react';

const propTypes = {
    /** @docs-ignore. */
    children: PropTypes.node,
};

function Footer(props) {
    const { children, ...other } = props;
    return (
        <tfoot {...other}>
            {children}
        </tfoot>
    );
}

Footer.splunkUiType = 'Table.Footer';
Footer.propTypes = propTypes;
export default Footer;



